import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { StaticImage } from "gatsby-plugin-image"

const NotFoundPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={location} title={siteTitle}>
      <h1 className="h1-aboutpage">About</h1>
      <StaticImage
        className="bio-avatar-about"
        layout="fixed"
        formats={["auto", "webp", "avif"]}
        src="../images/profile-pic-high.png"
        width={200}
        height={200}
        quality={100}
        alt="Profile picture"
      />
      <h2>七原 宇紀<br></br>Takanori Nanahara</h2>
      <p>
        2001年2月12日生まれ。<br></br>
        名古屋大学工学部 環境土木・建築学科 B4<br></br>
      </p>
      <h4>経歴</h4>
      <p>
        2019年4月 名古屋大学 工学部 環境土木・建築学科 入学<br></br>
        2023年4月 名古屋大学 大学院情報学研究科 心理・認知科学専攻 or 名古屋大学 大学院環境学研究科 都市環境学専攻 入学予定
      </p>
    </Layout>
  )
}

export const Head = () => <Seo title="About" />

export default NotFoundPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`